@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
header {
  position: fixed;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1882352941);
  transition: all ease 0.3s;
}
header .container .menu-header,
header .container .logo {
  display: flex;
  align-items: center;
}
header .container .menu-header {
  justify-content: flex-end;
}
header .container .menu-header #btn-header-orcamento {
  margin-right: 25px;
}
header .container .menu-header .icon-search {
  width: 20px;
  border-left: 1px solid rgba(255, 255, 255, 0.1882352941);
  padding: 20px 0 20px 25px;
  box-sizing: content-box;
}
header .container .menu-header .icon-search svg {
  cursor: pointer;
  transition: all ease 0.3s;
}
header .container .menu-header .icon-search svg:hover {
  transform: scale(1.2);
}
header .container .menu-header #secondary-menu-header {
  display: flex;
  align-items: center;
  margin-right: 25px;
}
header .container .menu-header #secondary-menu-header nav {
  margin: 0 15px;
}
header .container .menu-header #icon-menu {
  position: relative;
  width: 30px;
  height: 10px;
  margin-left: 25px;
  cursor: pointer;
}
header .container .menu-header #icon-menu #icon-menu-top:before,
header .container .menu-header #icon-menu #icon-menu-bottom:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all ease 0.3s;
}
header .container .menu-header #icon-menu #icon-menu-top:before {
  top: 0;
}
header .container .menu-header #icon-menu #icon-menu-bottom:before {
  bottom: 0;
}
header .container .menu-header #icon-menu:hover #icon-menu-top:before {
  top: -2px;
}
header .container .menu-header #icon-menu:hover #icon-menu-bottom:before {
  bottom: -2px;
}
header .container #div-search {
  position: absolute;
  width: 100%;
  height: 0;
  top: 110%;
}
header .container #div-search input {
  color: #006635;
  background: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  border-radius: 30px;
  padding: 0 15px;
  outline: none;
  height: 0;
  width: 100%;
  transition: all ease 0.3s;
}
header .container #div-search.active input {
  padding: 5px 15px;
  height: 40px;
}
header #menu-scroll {
  position: fixed;
  top: -90px;
  background-color: #fff;
  color: #006635;
  width: 100%;
  z-index: 100;
  transition: all ease 0.3s;
}
header #menu-scroll svg path {
  fill: #006635;
}
header #menu-scroll .container .menu-header .icon-search {
  border: none;
  padding: 20px 25px 20px 0;
}
header #menu-scroll .container .menu-header .icon-search svg path {
  fill: #cae098;
}
header #menu-scroll .container .menu-header .menu li .sub-menu {
  background: #fff;
  backdrop-filter: none;
  -webkit-backdrop-filter: none;
}
header #menu-scroll .container .menu-header .menu li.menu-item-has-children::after {
  color: #006635;
}
header #menu-scroll .container .menu-header .menu a {
  color: #006635;
}
header #menu-scroll .container .menu-header .button-orcamento {
  position: relative;
  padding: 0 19.5px;
  margin-right: 19.5px;
}
header #menu-scroll .container .menu-header .button-orcamento::before {
  content: "";
  position: absolute;
  top: -10%;
  right: 0;
  width: 1px;
  height: 120%;
  background-color: #cae098;
}
header #menu-scroll.active {
  top: 0;
}
header #menu-mobile {
  display: none;
}
header.menu-scroll {
  border: none;
}
header.menu-scroll .row-menu-header {
  opacity: 0;
  z-index: -1;
}
header.active-modal {
  top: -90px !important;
}
header.active-modal #menu-scroll {
  top: -90px !important;
}